
/* we used Line Graph using react-chartjs-2  */
import {Bar} from 'react-chartjs-2'
import {
  Chart as chartJs,
  Title,
  Tooltip,
  LineElement,
  Legend,
  CategoryScale,
  LinearScale,
  PointElement,
} from 'chart.js'
import React from 'react'
/* we need to register each element before use so using Chartjs.register we register each required element */

chartJs.register(Title, Tooltip, LineElement, Legend, CategoryScale, LinearScale, PointElement)
/*create your own graph design */
const PoGraph = (props: any) => {

  const {graphFlag, data, data1, options} = props
  

  return (
    
    <React.Fragment>
    
      {graphFlag && (
        <div className='row g-5 gx-xxl-8'>
          <div className='col-xxl-12'>
            <div className='card-xxl-stretch mb-5 mb-xxl-8'>
              <div className='row gy-5 g-xl-4'>
                <div className={`card`}>
                  <div className='card-header border-0 pt-2'>
                    <h3 className='card-title align-items-start flex-column'>
                      <span
                        className='card-label fw-bolder fs-3 mb-1'
                        style={{color: 'rgb(138, 38, 80)'}}
                      >
                        PO Graph
                      </span>
                    </h3>
                  </div>
                  <div
                    className='card-body py-3 h-600px '
                    style={{marginLeft: '5%', marginRight: '5%'}}
                  >
                    <Bar
                      data={data1.data != null ? data1.data : data.data}
                      width={50}
                      height={50}
                      options={options} 
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      ) }
    </React.Fragment>
  )
}
export default PoGraph
